	module.exports = {"data":{"post":{"html":"<p>Compared to yesterday, my to-do list for today is a breeze.</p>\n<p>1. Stumble on steps at S-Bahn station Sternschanze on the way to work and twist knee. Do not notice pain at first. When at noon walking becomes extremely uncomfortable, go home. Find that family doctor has no office hours this afternoon. Go to pharmacy and buy strongest non-prescription pain pills available. </p>\n<p>**Update:** My neighbor the physical therapist says that the fibula *(Wadenbein)* is way too flexible at the knee, and suspects a severe strain or a minor tear. She suggests I visit an orthopedist first thing in the morning. And since this was legally a work accident, I get to become acquainted with German workers' comp bureaucracy. Oh, joy!</p>","fields":{"title":"To-Do List","date":"25 May 2004","url":"https://papascott.de/2004-05-25-to-do-list/"}}},"pathContext":{"slug":"/2004-05-25-to-do-list/","prev":{"fields":{"title":"Seven tricks that Web users don't know","date":"25 May 2004","slug":"/2004-05-25-seven-tricks-that-web-users-dont-know/"}},"next":{"fields":{"title":"Typo3 for Mac OS X","date":"25 May 2004","slug":"/2004-05-25-typo3-for-mac-os-x/"}}}}

